import React, { useState } from 'react'
import Web3 from "web3";

import Vault from './Vault'

function App() {

    const [connected, setConnected] = useState(false)
    const [wallet, setWallet] = useState(null)
    const [nativeBalance, setNativeBalance] = useState(0.0)

    const connectWallet = async () => {
        if (window.ethereum) {
            const web3 = new Web3(window.ethereum)
            await window.ethereum.enable()
            let account = await web3.eth.getAccounts()
            setWallet(account[0])
            // console.log(account)
            let balance = await web3.eth.getBalance(account[0])
            balance = parseFloat(web3.utils.fromWei(balance))
            setNativeBalance(balance)
        }
    }

    return (
        <div className="bg-gray-600 h-screen w-screen">
            <div className="flex bg-white h-16 p-4 items-center sticky top-0">
                <p className='flex-none rounded-2xl text-xl sm:text-2xl'> WyanToken </p>
                <div className='flex-grow w-full'></div>
                {wallet == null &&
                    <button
                        className='p-2 flex-none rounded-3xl bg-red-300 text-white text-lg border-8 border-white'
                        onClick={connectWallet}
                    > Connect Wallet </button>
                }
                {wallet != null && <div className='ml-4 px-4 truncate rounded-3xl bg-red-300 text-white text-lg w-96 md:w-1/6'>{wallet}</div>}

            </div>
            <div className="flex flex-row flex-wrap bg-gray-600 justify-evenly py-10">
                <Vault token='WYN' nativeBalance={nativeBalance}/>
                <Vault token='BNB' nativeBalance={nativeBalance}/>
                <Vault token='API3' nativeBalance={nativeBalance}/>
            </div>
        </div>
    );
}

export default App;
